.root {
  width: 100%;
  min-height: 100%;
  // padding: 2rem 1rem;

  .btn {
    font-size: 0.875rem;
    color: #0969da;
    cursor: pointer;

    &:hover,
    &:focus {
      text-decoration: underline;
      outline: none;
    }
  }

  .projectWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0.5rem;
  }

  .title {
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.5rem;
    color: #1f2328;
  }

  .container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1rem;
    justify-items: center;
    width: 100%;
  }

  @media (max-width: 768px) {
    .projectWrap {
      flex-direction: column;
      align-items: flex-start;
      gap: 0.5rem;
    }

    .container {
      grid-template-columns: 1fr;
    }
  }
}