.root{width:100%;min-height:100%}.root .content{margin-bottom:1.25rem}.root .repoHeader{display:flex;justify-content:flex-start;align-items:center;margin:.625rem 0;border-bottom:1px solid rgba(27,31,35,.12)}.root .repoHeader h1{font-size:1.5rem}.root .repoHeader h1:before{content:"📦";margin-right:10px}.root .tagList{display:flex;flex-wrap:wrap;gap:.625rem;margin-top:1.25rem}.root .tagList.grey>div{background-color:#f6f8fa;color:#000}.root .tagList>div{background-color:#3d6aff;color:#fff;padding:.375rem .625rem;border-radius:50px;font-size:.875rem}.root .readme{font-size:1rem;line-height:2;color:#4b4b4b;border-radius:.375rem;padding:0 1.25rem 1.25rem;border:1px solid rgba(27,31,35,.12)}.root .readme .title{margin-bottom:21px}.root .readme .title>span{position:relative;padding:8px 0;display:flex;align-items:center;font-size:1.125rem;color:#000;font-weight:700}.root .readme .title>span:before{z-index:1;content:"";position:absolute;bottom:-1px;left:0px;width:120px;height:3px;background:#f78166;border-radius:2px}.root .readme .title>span::after{content:"";position:absolute;bottom:-1px;left:0;width:100%;height:2px;background:rgba(27,31,35,.12);border-radius:2px}