.root{width:100%;height:100%}.root .repoContainer{max-width:56.25rem;margin:0 auto;background-color:#fff;border-radius:.375rem}.root .repoHeader{display:flex;align-items:center;padding-top:1.25rem;border-bottom:1px solid rgba(27,31,35,.12);padding-bottom:1.25rem}.root .repoHeader.isStart{justify-content:flex-start}.root .repoHeader h1{font-size:1.5rem;display:flex;align-items:center}.root .repoHeader h1::before{content:"📦";margin-right:.625rem}.root .branch{display:inline-flex;align-items:center;gap:.375rem;border:1px solid #e5e7eb;border-radius:.375rem;padding:.375rem .75rem;background-color:#fff;box-shadow:rgba(0,0,0,.05) 0px 1px 2px;background-color:#f5f5f5;min-width:6.25rem;height:2.1875rem}.root .private{border:1px solid #d1d9e0;border-radius:.875rem;padding:.1875rem .3125rem;font-size:.8125rem;font-weight:600;background-color:#fff;margin-left:.5rem}.root .icon{display:inline-flex;align-items:center;gap:.25rem;margin-left:.9375rem;font-size:.875rem}.root .readme{font-size:1rem;line-height:2;color:#4b4b4b;border-radius:.375rem;padding:0 1.25rem 1.25rem;border:1px solid rgba(27,31,35,.12)}.root .readme .title{margin-bottom:21px}.root .readme .title>span{position:relative;padding:8px 0;display:flex;align-items:center;font-size:1.125rem;color:#000;font-weight:700}.root .readme .title>span:before{z-index:1;content:"";position:absolute;bottom:-1px;left:0px;width:120px;height:3px;background:#f78166;border-radius:2px}.root .readme .title>span::after{content:"";position:absolute;bottom:-1px;left:0;width:100%;height:2px;background:rgba(27,31,35,.12);border-radius:2px}.root .imgWrap{margin-bottom:1.75rem;width:100%}.root .imgWrap img{width:100%}.root .desc{margin-top:1rem;text-align:left}.root a{display:inline-block}.root a span{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;white-space:normal;line-height:1.5;max-height:1.5;text-align:left;word-wrap:break-word;word-break:break-all;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;margin-top:2rem;background-color:#2da44e;border:none;color:#fff;padding:6px 12px;border-radius:6px;cursor:pointer}.root a span:last-of-type{margin-top:1rem}